<script>
import AlertMixin from '@/mixins/commons/AlertMixin'
import LoadingMixin from '@/mixins/commons/LoadingMixin'
import FundsTransferService from '@/services/ft/FundsTransferService'
import WorkflowTaskTableMixin from '@/mixins/commons/WorkflowTaskTableMixin'
import FundsTransferChildrenTableMixin from '@/mixins/ft/FundsTransferChildrenTableMixin'

export default {
  name: 'funds-transfer-workflow-instance-view',
  mixins: [AlertMixin, LoadingMixin, WorkflowTaskTableMixin, FundsTransferChildrenTableMixin],
  props: {
    transferId: {
      type: String,
      default: null
    },
    viewOnly: {
      type: Boolean,
      default: false
    }
  },
  data: function () {
    return {
      workflowInstance: {},
      currencySummaryList: [],
      tasksVisible: false
    }
  },
  computed: {
    paymentFileDownloadUrl: function () {
      let baseUrl = process.env.NODE_ENV === 'development' ? 'http://localhost:8080' : ''
      let appId = this.$store.getters.appId
      let customerId = this.$store.getters.activeExternalId.externalId
      return `${baseUrl}/apps/Connect/DownloadSecure/downloadFundsTransferPaymentFile?sourceAppId=${appId}&customerId=${customerId}&transferId=${this.workflowInstance.transferId}`
    },
    isBulkTransfer: function () {
      return this.workflowInstance.typeBehaviourEnumId && this.workflowInstance.typeBehaviourEnumId.startsWith('FT_TYPE_BULK')
    }
  },
  methods: {
    async load () {
      let self = this
      this.startLoading('instance-loading-container')
      FundsTransferService.fundsTransferWorkflowInstances({
        instanceId: this.instanceId
      }).then(function (response) {
        self.stopLoading('instance-loading-container')
        self.workflowInstance = response.data.instanceList[0] || {}
        self.tasksVisible = true
        self.fundsTransferParentId = self.workflowInstance.transferId
        self.loadFundsTransferCurrencySummary()
        self.loadTaskTableData('tasks-loading-container')
        self.loadFundsTransferChildrenTableData('transfers-loading-container')
      })
    },
    loadFundsTransferCurrencySummary () {
      let self = this
      FundsTransferService.currencySummaries({
        transferId: this.workflowInstance.transferId
      }).then(function (response) {
        if (response.status === 200) {
          self.currencySummaryList = response.data.currencySummaryList || []
        }
      })
    },
    beneficiaryAccountNumber: function (item) {
      return item.beneficiaryIban ? item.beneficiaryIban : item.beneficiaryAccountNumber
    },
    cancel () {
      this.$router.back()
    },
    duplicate () {
      this.$router.push({ name: 'FundsTransferDuplicate', params: { id: this.workflowInstance.transferId } })
    }
  },
  mounted () {
    this.load()
  },
  watch: {
    $route: function (to, from) {
      this.load()
    },
    transferId: function () {
      this.load()
    }
  }
}
</script>
