import FundsTransferService from '@/services/ft/FundsTransferService'
import LoadingMixin from '@/mixins/commons/LoadingMixin'
import ListLayoutMixin from '@/mixins/commons/ListLayoutMixin'

let FundsTransferChildrenTableMixin = {
  mixins: [LoadingMixin, ListLayoutMixin],
  props: {
    defaultFundsTransferChildrenTableFilter: {
      type: String,
      default: null
    },
    defaultFundsTransferChildrenTableOrderBy: {
      type: String,
      default: null
    }
  },
  data: function () {
    return {
      fundsTransferParentId: '',
      fundsTransferChildrenTableData: [],
      fundsTransferChildrenTableFilterTimer: null,
      fundsTransferChildrenTableFilter: this.defaultFundsTransferChildrenTableFilter,
      fundsTransferChildrenTableOrderBy: this.defaultFundsTransferChildrenTableOrderBy,
      fundsTransferChildrenTableContainer: 'loading-container',
      fundsTransferChildrenTableBusy: false,
      fundsTransferChildrenTableTotalRows: 0,
      fundsTransferChildrenTableCurrentPage: 1,
      fundsTransferChildrenTablePerPage: 10
    }
  },
  computed: {
    fundsTransferChildrenTableNoDataText: function () {
      return this.fundsTransferChildrenTableBusy ? this.$t('LOADING') + ' ...' : this.$t('NO_DATA_FOUND')
    },
    fundsTransferChildrenTableTotalPages: function () {
      return Math.ceil(this.fundsTransferChildrenTableTotalRows / this.fundsTransferChildrenTablePerPage)
    },
    fundsTransferChildrenTablePagingInfo: function () {
      return this.pagingInfo(this.fundsTransferChildrenTableCurrentPage, this.fundsTransferChildrenTablePerPage, this.fundsTransferChildrenTableTotalRows)
    }
  },
  methods: {
    loadFundsTransferChildrenTableData (loadingContainerId) {
      let self = this
      this.fundsTransferChildrenTableBusy = true
      this.fundsTransferChildrenTableContainer = loadingContainerId || this.fundsTransferChildrenTableContainer
      this.startLoading(this.fundsTransferChildrenTableContainer)
      FundsTransferService.fundsTransferChildren({
        transferId: this.fundsTransferParentId,
        pageIndex: this.fundsTransferChildrenTableCurrentPage - 1,
        pageSize: this.fundsTransferChildrenTablePerPage,
        filter: this.fundsTransferChildrenTableFilter,
        orderByField: this.fundsTransferChildrenTableOrderBy
      }).then(function (response) {
        self.stopLoading(self.fundsTransferChildrenTableContainer)
        self.fundsTransferChildrenTableBusy = false
        self.fundsTransferChildrenTableTotalRows = response.data.totalRows
        self.fundsTransferChildrenTableData = response.data.fundsTransferList || []
      }).catch(function (error) {
        self.stopLoading(self.fundsTransferChildrenTableContainer)
        self.fundsTransferChildrenTableBusy = false
        self.$emit('show-alert', 'danger', error.response.data.errors)
      })
    },
    refreshFundsTransferChildrenTable () {
      this.loadFundsTransferChildrenTableData()
    },
    filterFundsTransferChildrenTable (filter) {
      let self = this
      if (this.fundsTransferChildrenTableFilter !== filter) {
        clearTimeout(this.fundsTransferChildrenTableFilterTimer)
        this.fundsTransferChildrenTableFilterTimer = setTimeout(() => {
          self.fundsTransferChildrenTableFilter = filter
          self.loadFundsTransferChildrenTableData()
        }, process.env.VUE_APP_SEARCH_DELAY)
      } else {
        clearTimeout(this.fundsTransferChildrenTableFilterTimer)
      }
    },
    orderFundsTransferChildrenTable (orderBy, active) {
      this.fundsTransferChildrenTableOrderBy = (active ? '' : '-') + orderBy
      this.loadFundsTransferChildrenTableData()
    }
  },
  watch: {
    fundsTransferChildrenTableCurrentPage: function () {
      this.refreshFundsTransferChildrenTable()
    },
    fundsTransferChildrenTablePerPage: function () {
      this.refreshFundsTransferChildrenTable()
    }
  }
}

export default FundsTransferChildrenTableMixin
